<style lang="less" scoped>
@import url("../../common/css/common.less");
.imageBox {
  position: relative;
  .payBtn {
    width: 60rpx !important;
    height: 60rpx;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -30rpx;
    margin-left: -30rpx;
  }
}
</style>

<template>
  <view class="imageWrap">
    <!-- 图片列表 -->
    <view class="imageContent" v-for="(item, index) in videoLists" :key="index">
      <view class="dateTitle">{{timer == item.date ? "今天" : item.date}}</view>
      <view class="imageMain">
        <view class="imageItem" v-for="(item2, index2) in item.lists" :key="index2">
          <view class="imageTimer">{{item2.timer}}</view>
          <view class="imageBox" @click="toSkipVideoPreview(item2)">
            <checkbox class="imageCheckbox" v-if="status" :checked="item2.ischecked" :data-date="item.date" :data-index="index2" @click.stop="getSelectEvent($event, item2)"/>
            <image src="../../static/image/xsdz.png" mode="widthFix"></image>
             <!-- 添加播放图标 -->
            <image class="payBtn" src="../../static/image/play.png"></image>
          </view>
        </view>
      </view>
    </view>
    <!-- 底部操作按钮 -->
    <view class="footer" v-if="status">
      <view class="footerItem" @click="shareImage">分享</view>
      <view class="footerItem" @click="deleteImage">删除</view>
      <view class="footerItem" @click="allCheckedImage">全选</view>
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        isAllChecked: false
      }
    },
    props:{
      videoLists: {
        type: Array,
        default: () => { return [] }
      },
      timer:{
        type: String,
        default: ""
      },
      status: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      status: {
        handler (newVal, oldVal) {
          this.videoLists.forEach((item) => {
            item.lists.forEach((item2, index2) => {
              item2.ischecked = false
            })
          })
        }
      }
    },
    methods: {
      // 点击跳转视频预览界面
      toSkipVideoPreview (item) {
        console.log(134, item)
        const previewUrl = item.fullPath
        this.$uniapi.goGavigate(`../previewVideo/previewVideo?previewUrl=${previewUrl}`)        
      },
      getSelectEvent (e) {
        const {index, date} = e.target.dataset
        this.videoLists.forEach((item) => {
          if (item.date === date) {
            item.lists.forEach((item2, index2) => {
              if (index2 === index) {
                item2.ischecked = !item2.ischecked
              }
            })
          }
        })
      },
      // 封装选中的数据列表
      getCheckedList () {
        let ischeckedArr = []
        this.videoLists.forEach((item) => {
          item.lists.forEach((item2, index2) => {
            if (item2.ischecked) {
              ischeckedArr.push(item2)
            }
          })
        })
        return ischeckedArr
      },
      // 分享图片
      shareImage () {
        let ischeckedArr = this.getCheckedList() // 选中的数据
        console.log(115, ischeckedArr)
      },
      // 删除本地视频
      deleteImage () {
        let ischeckedArr = this.getCheckedList() // 选中的数据
        console.log(115, ischeckedArr)
      },
      // 全选封装共用函数
      isAllCheckedFun (ischecked) {
        this.videoLists.forEach((item) => {
          item.lists.forEach((item2, index2) => {
            item2.ischecked = ischecked
          })
        })
        this.isAllChecked = ischecked
      },
      allCheckedImage () {
        if (!this.isAllChecked) {
          this.isAllCheckedFun(!this.isAllChecked)
        } else {
          this.isAllCheckedFun(!this.isAllChecked)
        }
      }
    }
  }
</script>




